body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.parallax {
    height: 100%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

h1 {
    margin-bottom: 10px;
}

/* button {
    border: 0.5px solid black;
    border-radius: 5px;
} */



/* mobile */
.App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 20px 1fr 20px;
    grid-template-rows: repeat(3, auto) 5px;
    grid-row-gap: 40px;
    font-family: "Avenir Next";
    text-align: center;
}

.wrapper {
    /* location inside App */
    grid-column: 2/3;
    grid-row: 2/3;
    /* its own grid layout */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    background-color: white;
    border-radius: 5px;
    text-align: center;
    padding-bottom: 25px;
    background-color: white;
}
/* end mobile */

@media (max-width: 1100px) {
    button {
        border: 0.5px solid black;
        border-radius: 5px;
        background-color: transparent;
    }
}

/* desktop */
@media (min-width: 1100px) {

.App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 50px 1fr auto;
    font-family: "Avenir Next";
}

.wrapper {
    /* location inside App */
    grid-column: 3/span 4;
    grid-row: 2/3;
    /* its own grid layout */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    background-color: white;
    border-radius: 5px;
    text-align: center;
    padding-bottom: 25px;
    background-color: white;
}

}
/* end desktop */

.individualQuestions {
    margin-bottom: 15px;
}

ol {
    margin: 25px;
    padding: 0;
    margin-bottom: 25px;
}

@media (min-width: 700px) {
ol {
    margin: 50px;
    padding: 0;
    margin-bottom: 25px;
}
}

.masterResultsDiv {
    margin-top: 25px;
}
